/* index.css */

body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
}

.cards {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  overflow-x: auto;
  gap: 20px;
  flex-wrap: wrap;
  white-space: nowrap;
  margin: 4px;
  padding: 4px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  white-space: nowrap;
  margin-bottom: 4px;
  justify-content: space-evenly;

}

.card {
  background-color: #fff;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.2s;
  width: 280px;
  margin: 10px;
}

.cards:hover {
  transform: translateY(-20px);
  
  transform: scale(1.05);
  border-color: #ff9900;
  border-width: 2px;
}
.card:hover {
  transform: translateY(-20px);
 
  transform: scale(1.05);
  border-color: #ff9900;
  border-width: 2px;
}

.card_img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.card_info {
  padding: 16px;
}

.card_category {
  font-size: 14px;
  color: #777;
}

.card_title {
  margin-top: 8px;
  font-size: 18px;
  color: #333;
}

.card_title:hover {
  color: #f44336;
}

.button {
  display: inline-block;
  margin-top: 10px;
  padding: 8px 16px;
  font-size: 14px;
  border: none;
  border-radius: 4px;
  background-color: #f44336;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.2s;
}

.button:hover {
  background-color: #4CAF50; /* Green */
  color: white;
  
}


.heading_style {
  font-size: 20px;
  padding:30px 0;
  text-align: center;
  font-family: "Raleway", sans-serif;
  text-transform:uppercase;
  font-size: 12px;
  letter-spacing: 2px;
  font-weight: bold;
  color: rgb(175, 16, 16);
  background-color: rgb(232, 243, 240);
  font-size: 1.5rem;
  margin-bottom: 30px;
}
.button_style{
  
    background-color: #7c6ff2; /* Green */
    border: none;
    border-radius: 50px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    
}
.button_style:hover{
  cursor: pointer;
}